.popup {
	@extend %popup;
	&.is-active {
		visibility: visible;
		opacity: 1; }
	@include md {
		padding: 60px 20px; }
	@include xs {
		padding: 60px 10px; }
	&__window {
		display: block;
		margin: auto;
		max-width: 808px;
		width: 100%;
		box-shadow: 0 0 21px #000000;
		border: 1px solid #29293c;
		background-color: #1b1b28; }
	&__body {
		font-size: 16px; }
	&__caption {
		padding: 22px 0;
		display: table;
		width: 100%;
		font-size: 14px;
		line-height: 1.2; }
	&__top {
		background-color: #181823;
		text-align: left;
		padding: 9px 44px;
		position: relative; }
	&__top-title {
		@include css-lock(22, 30, 900, 1920);
		@include roboto-light; }
	&__row {
		background-color: #1B1B28;
		padding: 12px 0px;
		display: table;
		width: 100%;
		&:nth-child(even) {
			background-color: #181823; } }
	&__col,
	&__caption-col {
		display: table-cell;
		vertical-align: middle;
		text-align: left;
		&_1 {
			width: 46%;
			padding-left: 44px; }
		&_2 {
			width: 35%; }
		&_3 {
			width: 19%;
			padding-right: 52px; } } }
.popup-offers {
	@extend %popup;
	@include md {
		padding: 60px 20px; }
	@include xs {
		padding: 60px 10px; }
	&.is-active {
		visibility: visible;
		opacity: 1; }
	@include sm {
		padding: 60px 10px;
		.popup__window {
			min-width: auto;
			overflow-x: auto; }
		.popup__col_1,
		.popup__caption-col_1 {
			padding-left: 15px; }
		.popup__col_3,
		.popup__caption-col_3 {
			padding-right: 15px; }
		.popup__top {
			padding: 9px 15px;
			min-width: 500px; }
		.popup__body {
			min-width: 500px; } } }
.product {
	display: table;
	&__media {
		display: table-cell;
		vertical-align: middle;
		width: 36px;
		height: 36px;
		overflow: hidden;
		img {
			display: block;
			max-width: 100%;
			max-height: 100%;
			margin: auto; } }
	&__desc {
		display: table-cell;
		vertical-align: middle;
		padding-left: 22px; } }
.flags {
	font-size: 0;
	i {
		&:not(:last-of-type) {
			margin-right: 16px; } } }
.popup-checkin {
	@extend %popup;
	padding: 100px;
	@include md {
		padding: 60px 20px; }
	@include xs {
		padding: 60px 10px; }
	&.is-active {
		visibility: visible;
		opacity: 1; }
	&__window {
		max-width: 864px;
		width: 100%;
		margin: auto; }
	&__top {
		text-align: left;
		@include clr; }
	&__col-1 {
		@include xs-min {
			float: left;
			max-width: 400px; } }
	&__col-2 {
		@include xs-min {
			float: right;
			text-align: right;
			max-width: 350px; }
		@include xs {
			margin-top: 10px; } }
	&__body {
		padding-top: 126px;
		@include sm {
			padding-top: 30px; } }
	&__sub {
		@include sm-min {
			padding-top: 8px; } }
	&__title {
		font-size: 48px;
		@include sm {
			font-size: 26px; } } }
.popup-login {
	@extend %popup;
	padding: 100px;
	@include md {
		padding: 60px 20px; }
	@include xs {
		padding: 60px 10px; }
	&.is-active {
		visibility: visible;
		opacity: 1; }
	&__window {
		max-width: 600px;
		width: 100%;
		margin: auto; }
	&__top {
		text-align: left; }
	&__body {
		padding-top: 20px;
		@include sm-min {
			padding-top: 40px;
			padding-left: 26px; }
		@include sm {
			@include xs-min {
				margin-left: -20px; } } }
	&__sub {
		padding-top: 8px; }
	&__title {
		font-size: 48px;
		@include sm {
			font-size: 26px; } } }
